import React, { FC } from 'react';
import { useForm } from 'react-hook-form';
import { RouteComponentProps } from 'react-router';
import { Col, Row } from 'reactstrap';
import { EditForm, EditViewProps, getSelectedItem, InputGroupInline, InputType, onlyNumber, WindowComponent } from 'summer';
import { CLASS_NAME, GrupoEmpresa } from '../../../../models/dtos/ger/grupoEmpresa';

interface GrupoEmpresaEditViewProps extends EditViewProps<GrupoEmpresa> {}

const GrupoEmpresaEditView: FC<GrupoEmpresaEditViewProps & RouteComponentProps> = props => {
  const selectedItem = getSelectedItem(props);

  const formProps = useForm<GrupoEmpresa>({ defaultValues: selectedItem, mode: 'onBlur' });

  const defaultWidth: number = 100;

  const validateExtras = (): string[] => {
    const erros = [];

    if (formProps.getValues().grupoEmpresaPai && formProps.getValues().id === formProps.getValues().grupoEmpresaPai.id) {
      erros.push('Grupo - O grupo superior não pode ser igual ao grupo atual.');
    }

    return erros;
  };

  return (
    <WindowComponent title="Manutenção de Grupos de Empresa" wikiId={1258}>
      <EditForm formProps={formProps} editViewProps={props} dtoClassName={CLASS_NAME} validateExtras={validateExtras}>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { ...onlyNumber(0, 8) } }}
              id="codigo"
              isPrependLabel={true}
              inputProps={{ autoFocus: true }}
              label="Código"
              labelWidth={defaultWidth}
              placeholder="Informe o Código do Grupo de Empresas"
              tooltipProps={{ message: 'Preenchimento Opcional - Codificação Automática' }}
              type={InputType.NUMBER}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { maxLength: 60, required: true } }}
              id="descricao"
              isPrependLabel={true}
              label="Descrição"
              labelWidth={defaultWidth}
              placeholder="Informe a Descrição do Grupo de Empresas"
            />
          </Col>
        </Row>
      </EditForm>
    </WindowComponent>
  );
};

export default GrupoEmpresaEditView;
